<template>
  <div class="data-manage">
    <Row class="index-show-content">
      <Col span="4" class="index-tree">
        <Tree :data="dataTree" @on-select-change="clickTreeNode"></Tree>
      </Col>
      <Col span="20" class="index-table">
        <!-- 检索 -->
        <query-form @initList="loadListData" :formValidate="formValidate" @resetForm="resetForm" :secondCityList="secondCityList"></query-form>
        <Table :columns="columns" :data="list" :loading="loading">
          <Button type="ghost" slot="header" class="m-marlr" @click="setNewData" v-if="false">生成数据</Button>
          <Button type="ghost" slot="header" class="m-marlr" @click="csvRows('excel')">导出excel</Button>
        </Table>
        <Page :total="total" show-total @on-change="pageChange" class="page"></Page>
      </Col>
    </Row>

    <!-- 生成数据 弹出框 -->
    <Modal v-model="isSetData" title="选择年份">
      <Select v-model="yearList" placeholder="请选择年份...">
        <Option :value="item" v-for="item in setYears" :key="item">{{item}}</Option>
      </Select>
      <div slot="footer">
        <Button type="primary" @click="sethandleSubmit">确定</Button>
        <Button type="ghost" @click="sethandleReset" style="margin-left: 10px">取消</Button>
      </div>
    </Modal>

  </div>
</template>

<script>
  import queryForm from './component/query.vue'
  import _ from 'lodash'
  import Cookies from 'js-cookie'
  export default {
    components: {
      queryForm
    },
    data() {
      return {
        formValidate: {
          year: new Date().getFullYear(),
          departmentId: null
        },
        dataTree: [],
        firstCityList: [],
        secondCityList: [],
        searchInfo: {},
        pageInfo: {
          pageNumber: 1,
          pageSize: 10
        },
        indexId: '',
        list: [],
        loading: false,
        nodeInfo: [],
        indexDataStatusChange: [{
          label: '发布',
          value: "1"
        }, {
          label: '草稿',
          value: "2"
        }, {
          label: '无效数据',
          value: "3"
        }, {
          label: '提交待审',
          value: "4"
        }],
        departmentIdList: [],
        isSetData: false,
        setYears: [],
        yearList: null,
        editList: {},
        userFlag: '',
        columns: [
          {
            title: '指标名称',
            key: 'name'
          },
          {
            title: '年份',
            key: 'year',
            width: 80,
          },
          {
            title: '单位',
            key: 'unitsName',
            width: 80
          },
          {
            title: '指标状态',
            key: 'indexDataStatus',
            width: 160,
            render: (h, params) => {
              let options = [], indexDataStatusChange = []
              if (this.userFlag === 'admin') {
                indexDataStatusChange = this.indexDataStatusChange
              } else if (this.userFlag === 'dataEntry') {
                indexDataStatusChange = this.indexDataStatusChange.forEach(el => {
                  if(!['2','4'].includes(el.value)){
                    this.$set(el, 'disabled', true)
                  }
                })
                indexDataStatusChange = this.indexDataStatusChange
              } else if (this.userFlag === 'review') {
                this.indexDataStatusChange.forEach(el => {
                  if(['4'].includes(el.value)){
                    this.$set(el, 'disabled', true)
                  }
                })
                indexDataStatusChange = this.indexDataStatusChange
              }
              indexDataStatusChange.forEach(item => {
                options.push(h('Option', {
                  props: {
                    value: item.value,
                    label: item.label,
                    disabled: item.disabled
                  }
                }))
              })
              return h('Select', {
                props: {
                  value: params.row.indexDataStatus,
                  size: 'small'
                },
                on: {
                  'on-change': (val) => {
                    this.$set(params.row, 'ischange', true)
                    this.$set(params.row, 'indexDataStatus', val)
                  }
                }
              }, options)
            }
          },
          {
            title: '指标数据',
            key: 'value',
            width: 100,
            render: (h, params) => {
              return h('Input', {
                props: {
                  value: params.row.value,
                  placeholder: '未录入',
                  size: 'small',
                  disabled: this.userFlag === 'review'
                },
                on: {
                  'input': (val) => {
                    this.$set(params.row, 'ischange', true)
                    this.$set(params.row, 'value', val)
                  }
                }
              })
            }
          },
          {
            title: '数据来源',
            key: 'departmentId',
            width: 250,
            render: (h, params) => {
              const options = [];
              this.departmentIdList.forEach(item => {
                options.push(h('Option', {
                  props: {
                    value: item.id,
                    label: item.departmentName,
                    disabled: item.departmentStatus === '2'
                  }
                }))
              })
              return h('Select', {
                props: {
                  value: params.row.departmentId,
                  size: 'small',
                  filterable: true,
                  disabled: this.userFlag === 'review'
                },
                on: {
                  'on-change': (val) => {
                    this.$set(params.row, 'ischange', true)
                    this.$set(params.row, 'departmentId', val)
                  }
                }
              }, options)
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              const submitRow = h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  disabled: !params.row.ischange
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    const reg = /^\d+(\.\d+)?$/
                    if(!reg.exec(params.row.value)){
                      this.$Message.warning('指标数据应为数字')
                      params.row.value = ''
                      return
                    }
                    this.$ajaxPost(this.projectName + '/mgmt/indexData/update.do', {
                      id: params.row.id,
                      value: params.row.value,
                      indexDataStatus: params.row.indexDataStatus,
                      departmentId: params.row.departmentId
                    }).then(res => {
                      this.$Message.success('设置成功')
                      this.loadListData(this.searchInfo)
                    })
                    this.$set(params.row, 'ischange', false);
                  }
                }
              }, '确定');
              return h('div', [submitRow]);
            }
          }
        ],
        total: 0
      }
    },
    created() {
      this.getParentListData()
      this.getCityLIstData()
      this.getDepartmentAllList()
      this.userFlag = JSON.parse(Cookies.get('Temp-User')).roleKey
      console.log(this.userFlag)
    },
    methods: {
      async getDepartmentAllList() {
        const res = await this.$ajaxPost(this.projectName + '/mgmt/department/findAll.do')
        this.departmentIdList = res
        // console.log(res)
      },
      // 获取市级，区县级 城市列表
      async getCityLIstData() {
        const res = await this.$ajaxPost(this.projectName + '/mgmt/city/findAll.do')
        // console.log(res)
        const allCityInfo = res
        const firstLevel = allCityInfo.filter(item => item.levelCode === '0') // 层级一
        const secondLevel = allCityInfo.filter(item => item.levelCode === '1') // 层级二
        const threeLevel = allCityInfo.filter(item => item.levelCode === '2') // 层级三
        firstLevel.forEach((el, index) => {
          if(!el.children){
              el.children = []
            }
          el.children.push(el)
        })
        // console.log(firstLevel)
        secondLevel.forEach((el, index) => {
          let self = {
            cityName: el.cityName,
            cityStatus: el.cityStatus,
            id: el.id,
            levelCode: el.levelCode,
            parentId: el.parentId,
          }
          if(!el.children){
            el.children = []
          }
          let newArr = threeLevel.filter(item => item.parentId === el.id)
          newArr.unshift(self)
          el.children = newArr
        })
        // console.log(secondLevel)
        this.secondCityList = [...firstLevel, ...secondLevel]
      },
      // 点击指标树节点
      async clickTreeNode(node) {
        // console.log(node)
        if(node.length === 0){
          this.indexId = ''
          return
        }
        this.nodeInfo = node
        this.indexId = node[0].id
      },
      // 点击生成数据 按钮
      setNewData() {
        this.isSetData = true
        this.getYearList()
      },
      // 点击生成数据 数据处理function (*****)
      formatSelectedData() {
        const params = []
        for (const k in this.selectedObject) {
          let ids = this.selectedObject[k].map(e => {
            return e.id;
          })
          params.push(...ids)
        }
        return params
      },
      // 点击生成数据 确定按钮
      sethandleSubmit() {
        if(this.yearList === null){
          this.$Message.warning('请选择年份...')
          return
        }
        this.$ajaxPost(this.projectName + '/mgmt/indexData/saveIndexData.do', {
          year: this.yearList
        }).then(response => {
          this.$Message.success('生成数据成功')
          this.sethandleReset()
        })
      },
      // 取消生成数据 取消按钮
      sethandleReset() {
        this.isSetData = false
        this.setYears = []
      },
      // 生成数据 年份选择 多选
      getYearList() {
        for (var i = new Date().getFullYear(); i >= 1949; i--) {
          this.setYears.push(i)
        }
      },
      // table在多选模式下有效，只要选中项发生变化时就会触发 (******)
      selectMethod(cs) {
        // console.log(cs)
        if (!(this.params.pageNumber in this.selectedObject)) {
          this.selectedObject[this.params.pageNumber] = []
        }
        this.selectedObject[this.params.pageNumber].splice(0, this.selectedObject[this.params.pageNumber].length, ...cs)
      },
     
      csvRows(str) {
        const params = this.formatSelectedData();
        const url = this.projectName + '/mgmt/indexData/exportExcel.do?locker=6f0c077ee53d4aa68b9eae5b3e853bb8'
        window.open(url, "_self")
      },
      // 点击搜索确定按钮
      async loadListData(params) {
        // console.log(params)
        this.searchInfo = params
        this.loading = true
        const res = await this.$ajaxPost(this.projectName + '/mgmt/indexData/finds.do', {
          ...this.searchInfo,
          ...this.pageInfo,
          indexId: this.indexId
        })
        // console.log(res)
        this.loading = false
        this.list = res.rows
        this.total = res.total
      },
      // 重置按钮
      resetForm(data) {
        // console.log(data)
        if(data) {
          this.indexId = ''
        }
        this.$set(this.nodeInfo[0], 'selected', false)
      },
      checkedSelected() {
        if (!(this.selectedObject[this.params.pageNumber] && this.selectedObject[this.params.pageNumber].length)) return;
        this.selectedObject[this.params.pageNumber].forEach(e => {
          const tem = _.filter(this.list, _.matches({
            id: e.id
          }));
          if (tem.length) tem[0]._checked = true;
        })
      },
      initCombox() {
        this.loadConfig().then(res => {
          this.combobox = res.combobox;
        })
      },
      // 分页
      pageChange(page) {
        this.pageInfo.pageNumber = page
        this.loadListData(this.searchInfo)
      },
      // 获取指标体系属性图数据
      async getParentListData() {
        const dataTreeInfo = []
        let rootObj = {
          title: '全部指标',
          parentId: 0,
          id: 0,
          expand: true
        }
        const res = await this.$ajaxPost(this.projectName + '/mgmt/index/findAllIndex.do')
        // console.log(res)
        const parentFormatData = res
        parentFormatData.forEach(el => {
          this.$set(el, 'title', el.name)
          this.$set(el, 'expend', true)
        })
        // 获得最低层级数
        const indexNumber = parentFormatData.map(item => item.nodeIds.split(',').length).sort().reverse()[0]
        const arr = []
        for(let a = 1; a<=indexNumber; a++){
          arr.push(parentFormatData.filter(item => item.nodeIds.split(',').length === a))
        }
        for(let j = 1; j<arr.length; j++){
          this.handleParentData(arr[j-1], arr[j])
        }
        // console.log(arr[0])
        rootObj.children = arr[0]
        dataTreeInfo.push(rootObj)
        this.dataTree = dataTreeInfo
      },
      // 联级数据处理
      handleParentData(pre, next) {
        let str = ''
        return pre.forEach(ele => {
          next.forEach(k => {
            str = k.nodeIds.split(',').slice(0, k.nodeIds.split(',').length - 1).join(',')
            if(ele.nodeIds === str) {
              if(!ele.children){
                ele.children = []
              }
              ele.children.push(k)
            }
          })
        })
      }
    }
  }
</script>

<style lang='scss'>
  .data-manage{
    border: 1px solid #e9eaec;
    .ivu-radio-group{
      display: block !important;
      height: 50px;
      line-height: 50px;
      box-sizing: border-box;
      padding: 0 10px;
      border-bottom: 1px solid #e9eaec;
      span.text{
        display: inline-block;
        height: 50px;
        line-height: 50px;
        vertical-align: bottom;
      }
      .ivu-radio-wrapper{
        margin: 0 28px 0 10px;
      }
    }
    .index-show-content{
      height: 720px;
      .index-tree{
        height: 100%;
        border-right: 1px solid #e9eaec;
        box-sizing: border-box;
        padding: 10px 20px;
      }
      .index-table{
        box-sizing: border-box;
        padding: 10px 20px;
      }
    }
  }
  .page {
    float: right;
    margin: 20px 0;
  }
  .m-marlr {
    margin-left: 16px;
  }
</style>



