<template>
  <figure>
    <div :ref="`charts1`" :id="`charts1`" class="charts-box"></div>
    <Modal v-model="modal" :title="`${title}管理员信息`" @on-ok="modal=false" @on-cancel="modal=false" class="info-wrap">
      <p><span>账号：</span><i>{{info.account||'暂无'}}</i></p>
      <p><span>姓名：</span><i>{{info.name||'暂无'}}</i></p>
      <p><span>办公电话：</span><i>{{info.officePhone||'暂无'}}</i></p>
      <p><span>邮箱：</span>
        <Poptip trigger="hover" content="点击可给该管理员发送邮件提醒!" placement="bottom">
          <i class="email-wrap" @click="sendMessage(info.accountId)">{{info.email||'暂无'}}</i></Poptip>
      </p>
    </Modal>
  </figure>
</template>
<style>
  .echarts {
    height: 300px;
  }
</style>

<script>
  import echarts from 'echarts'
  import 'echarts/map/js/china.js'
  import 'echarts/theme/macarons.js'
  import position from './data/options.js'
  export default {
    data: function() {
      return {
        barData: {},
        info: {
          account: '',
          name: '',
          officePhone: '',
          email: ''
        },
        modal: false,
        title: ''
      }
    },
    mounted() {
      this.$ajaxPost(this.projectName + '/mgmt/indexData/findMapData.do').then(res => {
        this.barData = res;
        position.xAxis[0].data = res.cityList;
        position.series[0].data = res.entryList;
        position.series[1].data = res.noEntryList;
        this.initmap();
      })
    },
    methods: {
      sendMessage(id) {
        this.$ajaxPost(this.projectName + '/mgmt/access/sendEmail.do', {
          id: id
        }).then(res => {
          this.$Message.success('发送邮件成功！');
        });
      },
      initmap(res) {
        const node = document.getElementById(`charts1`);
        var myChart = echarts.init(node, 'macarons');
        myChart.setOption(position);
        myChart.on('click', (params) => {
          if (params.componentType === 'series') {
            if (params.seriesType === 'bar') {
              for (let key in this.barData.accountList[params.dataIndex]) {
                this.info[key] = this.barData.accountList[params.dataIndex][key];
              }
              this.title = this.barData.cityList[params.dataIndex];
              this.modal = true;
            }
          }
        });
      }
    }
  }
</script>
<style scoped>
  .charts-box {
    width: 100%;
    min-height: 640px;
    margin-top: 20px;
    overflow: hidden;
    padding: 20px 24px 20px;
  }
  .info-wrap p {
    height: 40px;
    line-height: 40px;
    font-size: 15px;
  }
  .info-wrap i {
    font-weight: 700;
  }
  .info-wrap span {
    width: 88px;
    display: inline-block;
  }
  .email-wrap {
    color: #57a3f3;
    cursor: pointer;
  }
</style>




