<template>
  <Form ref="formValidate" :model="formValidate">
    <Row>
      <Col :span="10">
        <FormItem prop="cityId">
          <span>区域<span style="color: #f56c6c">（必选）</span></span>
          <Select v-model="formValidate.cityId">
              <OptionGroup :label="i.cityName" v-for="i in secondCityList" :key="i.id">
                  <Option v-for="item in i.children" :value="item.id" :key="item.id+20">{{ item.cityName }}</Option>
              </OptionGroup>
          </Select>
        </FormItem>
      </Col>
      <Col :span="7">
        <FormItem prop="year">
          <span>年份<span style="color: #f56c6c">（必选）</span></span>
          <Select v-model="formValidate.year" placeholder="选择年份..." :clearable="true">
            <Option :value="item" v-for="item in optionsYearList" :key="item"></Option>
          </Select>
        </FormItem>
      </Col>
      <Col :span="5">
        <FormItem class="m-txt-right">
          <Button type="primary" @click="handleSubmit('formValidate')">确定</Button>
          <Button type="ghost" @click="handleReset" style="margin-left: 8px">重置</Button>
        </FormItem>
      </Col>
    </Row>
  </Form>
</template>
<script>
  export default {
    props: ['formValidate', 'secondCityList'],
    data() {
      return {
        searchInfo: {},
        optionsYearList: []
      }
    },
    created() {
      this.getYearList()
    },
    methods: {
      // 获取年份列表
      getYearList() {
        for (var i = new Date().getFullYear(); i >= 1949; i--) {
          this.optionsYearList.push(i);
        }
      },
      handleSubmit(name) {
        if(!this.formValidate.year){
          this.$Message.warning('请选择年份...')
          return
        }
        this.searchInfo = {}
       
        if(!this.formValidate.cityId){
        this.$Message.warning('请选择区域...')
        return
        }
        this.$set(this.searchInfo, 'year', this.formValidate.year)
        this.$set(this.searchInfo, 'cityId', this.formValidate.cityId)
        this.$emit('initList', this.searchInfo)
      },
      handleReset(name) {
        this.$refs['formValidate'].resetFields()
        this.$emit('resetForm', true)
      }
    }
  }
</script>
<style lang="scss" scoped>
  .m-txt-right {
    text-align: right;
  }
  .ivu-form-item{
    margin-bottom: 10px;
  }
  .ivu-select{
    width: 65%;
  }
</style>



